import { useState, useEffect, useRef, React } from 'react';
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import LoginButton from "../ui/LoginButton";
import MenuButton from "../ui/MenuButton";
import {LoginToken} from "../reference/GlobalVariable";

const Wrapper = styled.div`
    position: fixed;
    z-index: 3;
    top: 0; left: 0;
    padding: 15px; // 여백은 16으로
    width: calc(100% - 30px); // 너비는 100에서, 16*2 뺌
    min-width: 2000px; // 이거 이하로는 안줄어듬
    height: 70px;
    color: #CFFD5D; // 글자 색
    background: #100E1B; // 배경 색
`;

const CompanyName = styled.div`
    width: 1500px; // 너비는 전체의 50
    font-size: 24px; // 글자 크기
    font-weight: bold; // 글자 진하게
    text-align: left; // 글자 정렬, 왼쪽부터
    display: inline-block; // 이거 있어야, 다음 블록 오른쪽으로 넘어감
`;

const LoginMenu = styled.div`
    width: 50%; // 너비는 전체의 50
    font-size: 24px; // 글자 크기
    font-weight: bold; // 글자 진하게
    text-align: left; // 글자 정렬, 오른쪽부터
    display: inline-block;
`;

const SelectMenu = styled.div`
    font-size: 24px;
    font-weight: bold;
    //align-items: center;
    //justify-content: center;
    //text-align: center;
`;

function MainPageHeader(props) {
    const navigate = useNavigate();
    const [change, setchange] = useState(false);

    const handleChangeValue = () => {
        //setchange((change) => !change);
        if(LoginToken.data == 1)        {
            LoginToken.useData = 0;
        }
        else{
            LoginToken.useData = 1;
        }
    }   

    console.log('[MainPageHeader]');
    console.log('LoginToken.data: ', LoginToken.data);
    
    return (
        <Wrapper>
            <CompanyName>
                안녕하세요, 입시력전 컨설팅입니다!  &nbsp;&nbsp;&nbsp;&nbsp;                
                <LoginButton
                    title='로그인/아웃'
                    onClick={() => {
                        navigate("/");
                        handleChangeValue();
                        window.location.reload();
                        //console.log("/");
                    }}
                />
            </CompanyName>
            <LoginMenu>                   
            </LoginMenu>

            <hr/>
        </Wrapper>        
    );
}

export default MainPageHeader;