import React from "react";
import { useState, useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import {메뉴버튼_버튼클릭} from "../reference/GlobalVariable";

const StyledButton = styled.button`
    padding: 8px 16px;
    font-size: 16px;
    font-family: Malgun Gothic;
    font-weight: bold;
    border-width: 1px;
    border-radius: 8px;
    cursor: pointer;    
    
    color: #CFFD5D;
    background: #100E1B;
    
    :hover {
        color: #100E1B;
        background: #F7792C;
    }
`;

function MenuButton(props) {
    const {title, buttonState, onClick, handleButtonClick} = props;
    const navigate = useNavigate();
    const [change, setchange] = useState(false);
    
    // 변수명 숫자화
    const 수시등급겁색기 = 0;
    const 수시경쟁률검색기 = 1;
    const 수시충원율검색기 = 2;
    const 정시합격치트_LITE = 3;
    const 정시합격치트_PRO = 4;
    var buttonNum;

    //console.log('[MenuButton], title: ', title);
    
    if(title == '수시등급검색기'){
        buttonNum = 수시등급겁색기;
        //navigate("/수시경쟁률검색기");
    }
    else if(title == '수시경쟁률검색기'){
        buttonNum = 수시경쟁률검색기;
    }
    else if(title == '수시충원율검색기'){
        buttonNum = 수시충원율검색기;
    }
    else if(title == '정시합격치트(LITE)'){
        buttonNum = 정시합격치트_LITE;
    }
    else if(title == '정시합격치트(PRO)'){
        buttonNum = 정시합격치트_PRO;
    }

    const handleClick = () => {
        handleButtonClick();
    }    

    const onClickButton = () => {
        setchange((change) => !change);
        if(title == '수시등급검색기')
        {
            메뉴버튼_버튼클릭[수시등급겁색기] = true;  
            메뉴버튼_버튼클릭[수시경쟁률검색기] = false;  
            메뉴버튼_버튼클릭[수시충원율검색기] = false; 
            메뉴버튼_버튼클릭[정시합격치트_LITE] = false; 
            메뉴버튼_버튼클릭[정시합격치트_PRO] = false; 
        }
        else if(title == '수시경쟁률검색기')
        {
            메뉴버튼_버튼클릭[수시등급겁색기] = false;  
            메뉴버튼_버튼클릭[수시경쟁률검색기] = true;  
            메뉴버튼_버튼클릭[수시충원율검색기] = false; 
            메뉴버튼_버튼클릭[정시합격치트_LITE] = false; 
            메뉴버튼_버튼클릭[정시합격치트_PRO] = false; 
        }
        else if(title == '수시충원율검색기')
        {
            메뉴버튼_버튼클릭[수시등급겁색기] = false;  
            메뉴버튼_버튼클릭[수시경쟁률검색기] = false;  
            메뉴버튼_버튼클릭[수시충원율검색기] = true; 
            메뉴버튼_버튼클릭[정시합격치트_LITE] = false; 
            메뉴버튼_버튼클릭[정시합격치트_PRO] = false; 
        }
        else if(title == '정시합격치트(LITE)')
        {
            메뉴버튼_버튼클릭[수시등급겁색기] = false;  
            메뉴버튼_버튼클릭[수시경쟁률검색기] = false;  
            메뉴버튼_버튼클릭[수시충원율검색기] = false; 
            메뉴버튼_버튼클릭[정시합격치트_LITE] = true; 
            메뉴버튼_버튼클릭[정시합격치트_PRO] = false; 
        }
        else if(title == '정시합격치트(PRO)')
        {
            메뉴버튼_버튼클릭[수시등급겁색기] = false;  
            메뉴버튼_버튼클릭[수시경쟁률검색기] = false;  
            메뉴버튼_버튼클릭[수시충원율검색기] = false; 
            메뉴버튼_버튼클릭[정시합격치트_LITE] = false; 
            메뉴버튼_버튼클릭[정시합격치트_PRO] = true; 
        }
    };       
    
    return(
        <StyledButton 
            style = {{background: 메뉴버튼_버튼클릭[buttonNum]? "#CFFD5D" : "",
                    color: 메뉴버튼_버튼클릭[buttonNum]? '#100E1B': ""
                    }}
            
            onClick = {() => {
                onClickButton();
                handleClick();}} >
            
            {title || "Button"}   
        </StyledButton>
    ) 
}

export default MenuButton;