import { useState, useEffect } from 'react';
import styled from "styled-components";
//import {useAsync} from 'react-async'; -> ID값 입력 필요
import useAsync from './useAsync';
import * as gv from "./GlobalVariable";

const Wrapper = styled.div`
    width: 100%;
    min-width: 1500px; // 이거 이하로는 안줄어듬
    color: #CFFD5D;
    background: #100E1B;
`;

async function FetchData(url){
  console.log("[FetchData]")
  //const response = await fetch('https://raw.githubusercontent.com/S25ngho/ipsy/main/susi_example.csv')
  const response = await fetch(url)
  const data = await response.text();
  return data;
}

async function ParseData(url) {
  let text;
  console.log("[ParseData]")
  try{
    text = await FetchData(url);

    const csvData = []; 
    const lines = text.split("\n"); 

    for (let i = 0; i < lines.length; i++) {
      csvData[i] = lines[i].split(",");
    }   
    return csvData;
  }
  catch(error){
    console.log(error);
  }
}

function ReadData(props) {
  //const [userId, setUserId] = useState(null);
  const {link, dataName} = props;
  const [state, refetch] = useAsync(() => ParseData(link), [link], true);
  const { loading, data: csvData, error } = state; // state.data 를 users 키워드로 조회

  console.log("[ReadData]");
  //console.log("csvData: ", csvData);

  if (loading){
    console.log('로딩중');
    return <Wrapper>&nbsp;&nbsp;&nbsp;로딩중..</Wrapper>;
  }    
  if (error){
    console.log('에러가 발생했습니다.');
    return <Wrapper>&nbsp;&nbsp;&nbsp;에러가 발생했습니다</Wrapper>;
  }
  if (!csvData) return <Wrapper>&nbsp;&nbsp;&nbsp;로딩중..</Wrapper>;
  //if (!csvData) return <button onClick={refetch}>&nbsp;&nbsp;&nbsp;불러오기</button>;
  

  //console.log('csvData[5]: ', csvData[csvData.length-1]);// 5부터 불러옴
  gv.csvData_설문.useData = csvData

  return;  
}

export default ReadData; 