import React from "react";
import { useState, useEffect } from 'react';
import styled from "styled-components";

const StyledButton = styled.button`
    padding: 5px 5px; // 세로, 가로 여백
    font-size: 10px; // 글자 크기
    font-family: Malgun Gothic; // 글자 형식
    font-weight: bold;
    border-width: 1px;
    border-radius: 8px;
    cursor: pointer;
    
    color: ${({버튼색}) => { // 조건분기 하여 적용
        if (버튼색 == "검정색") return '#CFFD5D;'
    }};
    background: ${({버튼색}) => { // 조건분기 하여 적용
        if (버튼색 == "검정색") return '#100E1B'
        else return '#CFFD5D'}    
    }};
    :hover {
        color: ${({버튼색}) => { // 조건분기 하여 적용
            if (버튼색 == "검정색") return '#100E1B'    
        }};
        background: ${({버튼색}) => { // 조건분기 하여 적용
            if (버튼색 == "검정색") return '#CFFD5D'
            else return '#F7792C'}    
        }};
    };
`;

function LoginButton(props) {
    const {title, onClick, style, 색} = props;

    const handleClick = () => {
        //handleButtonClick();
    }    
    console.log('[LoginButton]');

    return(
        <StyledButton 버튼색 = {색}
            onClick = {onClick}
            style = {style}>
            {title || "Button"}   
        </StyledButton>
    ); 
}

export default LoginButton;