import { useState, useEffect, useRef, React } from 'react';
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import MenuArrange from './MenuArrange';
import ViewData from "./ViewData";
import LoginPage from "./LoginPage";
import {LoginToken} from "../reference/GlobalVariable";

const Wrapper = styled.div`
    width: calc(100% - 0px); // 너비는 100에서, 16*2 뺌
    min-width: 2000px; // 이거 이하로는 안줄어듬
    height: calc(100vh - 0px);
    //min-height: 2000px; // 이거 이하로는 안줄어듬
    margin-right: 15px;
    font-size: 24px; // 글자 크기
    color: #CFFD5D; // 글자 색
    background: #100E1B; // 배경 색
`;

const MenuPart = styled.div`
    position: fixed;
    z-index: 3;
    top: 100px;
    padding-left: 15px; // 여백은 15로
    padding-right: 15px; // 여백은 15로

    width: calc(100% - 30px);
    min-width: 1000px; // 이거 이하로는 안줄어듬
    height: 70px;
    font-size: 8px; // 필터 이름의 글자 크기
    font-weight: bold; // 글자 두께
    background: #100E1B; // 배경 색
`;

