import { useState, useEffect, useRef, React } from 'react';
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import * as gv from "../reference/GlobalVariable";
//import {authGoogleSheet, FetchData} from "../reference/GoogleSpread";
//import useGoogleSheet from "../reference/GoogleSpread.ts";


const Wrapper = styled.div`
    position: fixed;
    top: 100px;
    z-index: 3;
    width: 100%;
    height: calc(100% - 200px); // 이거 이하로는 안줄어듬

    font-size: 24px;
    font-weight: bold;

    color: #CFFD5D; // 글자 색
    background: #100E1B; // 배경 색
    display: flex;
	justify-content : center;
`;

const LoginForm = styled.form`
    padding-left:100px;
    padding-right:100px;
    justify-content: center;
    display: flex;
    flex-direction: column;
    width: 30%;
    font-size: 24px;
    font-weight: bold;
    background: black; // 배경 색
`;

const StyledInput = styled.input`
    height: 50px;
    font-size: 24px;
    font-weight: bold;
`;

const StyledButton = styled.button`
    height: 50px;
    font-size: 24px;
    font-weight: bold;
`;


function LoginPage(props) {
    const navigate = useNavigate();

    const [Email, setEmail] = useState("")
    const [Password, setPassword] = useState("")
    const [LoginToken_tmp, setLoginToken] = useState("Email/Password를 입력하세요")

    const [change, setchange] = useState(false);
    const {handleButtonClick_upper} = props;
    //console.log("[MainPageContainer]");

    
    //authGoogleSheet();
    //console.log(ReadData("https://raw.githubusercontent.com/S25ngho/ipsy/main/수시등급검색기.csv"));
    //const [data] = useGoogleSheet("#gid=1658768052");

    const handleChangeValue = () => {
        setchange((change) => !change);
        handleButtonClick_upper();
    }     

    const onEmailHandler = (event) => {
        setEmail(event.currentTarget.value)
    }
    const onPasswordHandler = (event) => {
        setPassword(event.currentTarget.value)
    }

    const ID_PW = ['aaaa@naver.com', '1234'];

    const onSubmitHandler = (event) => {
        event.preventDefault();
        
        // var PasswordTmp = ''
        // for(var i = 0; i < Email.indexOf('@'); i++)
        // {
        //     console.log('i:', i);
        //     console.log(Email.substr(0, Email.indexOf('@')).charCodeAt(i).toString(16)); // ID 부분만
        //     PasswordTmp = 
        //         PasswordTmp.concat(Email.substr(0, Email.indexOf('@')).charCodeAt(i).toString(16));
        //     console.log('PasswordTmp: ', PasswordTmp);
        // }

        if (Email.length == 0){
            setLoginToken('Email을 입력해주세요.');
        }
        else if (Password.length == 0){
            setLoginToken('비밀번호를 입력해 주세요.');
        }
        else {
            for(var i = 5; i < gv.csvData_설문.useData.length; i++)
            {     
                
                //console.log("i: ", i, "Email", Email, "DB_Email", gv.csvData_설문.useData[i][4]);
                //console.log("i: ", i, "Password", Password, "DB_Password", gv.csvData_설문.useData[i][5]);
                if(Email == gv.csvData_설문.useData[i][4] && Password == gv.csvData_설문.useData[i][5]){
                    gv.LoginToken.useData = 1;
                    setLoginToken('로그인 중...');
                    handleChangeValue();
                }
                if(gv.LoginToken.useData != 1 && i == gv.csvData_설문.useData.length -1){
                    setLoginToken('비밀번호가 불일치합니다.');
                }
            }
        }
        
        // if (Password == PasswordTmp)
        // {
        //     LoginToken.useData = 1;
        //     setLoginToken('로그인 중...');
        //     //navigate("/");
        //     handleChangeValue();
        // }
        // else {
        //     setLoginToken('비밀번호가 불일치합니다.');
        // }
    }   

    return (
        <Wrapper>
            <LoginForm onSubmit={onSubmitHandler}>
                <label> Email</label><br />
                <StyledInput type="email" value={Email} onChange={onEmailHandler} /><br />
                <label> Password</label><br />
                <StyledInput type="Password" value={Password} onChange={onPasswordHandler} />
                <br />
                
                <StyledButton>
                    Login
                </StyledButton>
                <br />

                <a href = "https://venetncompany.typeform.com/to/oowRpMaH" target = "blank  " > 회원가입 </a>
                <br />
 
                {LoginToken_tmp}
                <br />

            </LoginForm>
            <br></br>            

        </Wrapper>
    );
}

export default LoginPage;